main{padding-top:76px}#nav{position:fixed;background:#f6f6f6;width:calc(100% - 2.4rem);height:76px;box-shadow:2px 2px 5px #aaa;display:inline-block;overflow:hidden;display:flex;align-items:center;justify-content:space-between;text-transform:uppercase;list-style-type:none}#nav *{margin:0}#nav #logo-text{display:flex;flex-direction:column;justify-content:space-between;margin-left:-1.2rem}#nav #logo-text .title{font-size:2.5em;letter-spacing:5px;line-height:0.8;font-weight:700}#nav #logo-text #blurb{font-size:1.063em}#nav li{margin:0;padding:0;float:left}#nav #links a{transition:background-color .3s;display:block;padding:0 2vw;line-height:76px}#nav #links a:hover{background-color:rgba(0,0,0,0.1)}footer{background-color:#21618c;color:#eee}footer #logo-text{text-align:left;display:flex;text-transform:uppercase;flex-direction:column}footer #logo-text .title{font-size:3em;letter-spacing:4px;line-height:0.8}footer #logo-text #blurb{font-size:1.2em;letter-spacing:1px;font-weight:300}footer .column h5{text-transform:uppercase;font-size:1.5em}footer .column p{margin-bottom:0}footer .column .mdi-set{display:flex;flex-direction:row}footer .column .mdi-set a{font-size:3em;margin:1rem}#intro{text-align:center;padding:5rem;flex-direction:column;background-image:url("https://cdn.pbrd.co/images/HNjLWSA.png");background-repeat:no-repeat;background-size:auto 200%;background-position:left center}#project{background-color:#2e86c1;color:#eee}#project .text{align-self:flex-start;margin-right:5%}#project img{max-width:40%}#robots{background-color:#85c1e9;color:#fff}#robots .title{text-align:center}#robots *{flex:50%}
